<template>
    <q-page padding class="row items-center justify-center">
        <div class="row full-width">
            <div class="col-md-8 offset-md-2 col-xs-12 q-pl-md q-pr-md q-pt-sm">
                <q-card flat class="bg-white text-black">
                    <div class="row">
                        <div class="col-md-6 col-xs-12 q-pa-md">
                            <q-img placeholder-src="@/assets/logo.png" src="@/assets/logo.png"
                                spinner-color="white"></q-img>
                        </div>
                        <div class="col-md-6 col-xs-12">
                            <div class="q-pa-md">
                                <div class="text-h6 q-pb-md text-blue-8 text-center text-weight-bolder">
                                    Login as administrator
                                </div>
                                <q-form @submit="onSubmit" @reset="onReset" class="q-gutter-md">
                                    <q-input filled v-model="username" label="Username" lazy-rules :rules="[
                                        (val) =>
                                            (val && val.length > 0) || 'Username is required!',
                                    ]" />
                                    <q-input filled :type="showPassword ? 'text' : 'password'" v-model="password"
                                        label="Password" lazy-rules :rules="[
                                            (val) =>
                                                (val !== null && val !== '') ||
                                                'Password is required!',
                                        ]">
                                        <div class="icon-container">
                                            <q-icon v-if="showPassword" name="visibility"
                                                @click="togglePasswordVisibility" />
                                            <q-icon v-else name="visibility_off" @click="togglePasswordVisibility" />
                                        </div>
                                    </q-input>
                                    <div>
                                        <q-btn label="Login" type="submit" color="primary" />
                                        <q-btn label="Reset" type="reset" color="primary" flat class="q-ml-sm" />
                                    </div>
                                </q-form>
                            </div>
                        </div>
                    </div>
                </q-card>
            </div>
        </div>
    </q-page>
</template>
<script >
export default {
    data() {
        return {
            username: null,
            password: null,
            err: null,
            showPassword: false,
        };
    },
    methods: {
        onSubmit() {
            let user = {
                username: this.username,
                password: this.password
            }
            this.$axios.post('/api/user/login', user).then(async (response) => {
                if (response.data.status !== 'ok') {
                    this.$q.notify({
                        message: `${response.data.message}`,
                        color: 'red'
                    })
                } else {
                    let user = response.data.user;
                    let token = response.data.token;
                    await this.$q.notify({
                        message: `${response.data.message}`,
                        color: 'green-5'
                    })
                    await this.$store.dispatch('login', user)
                    await this.$store.dispatch('token', token)
                    await this.$router.push('/users')
                }
            })
        },
        onReset() {
            this.username = null;
            this.password = null;
        },
        togglePasswordVisibility() {
            this.showPassword = !this.showPassword;
        },

    },
};
</script>

<style scoped>
.icon-container {
    display: flex;
    align-items: center;
}

.q-btn {
    transition: transform 0.3s;
}

.q-btn:hover {
    transform: translateY(-3px);
}

.q-icon {
    transition: transform 0.3s;
}

.q-icon:hover {
    transform: scale(120%);
    cursor: pointer;
}
</style>